import { Component, OnInit } from '@angular/core';
import { HelperService, ThemeService } from '@app/services';
import { Book } from '@app/models';
import { IndexeddbService } from '@app/services/common/indexeddb.service';
import { DownloadHadithModalComponent } from '@app/components/component/download-hadith-modal/download-hadith-modal.component';
import { NoopScrollStrategy } from '@angular/cdk/overlay';
import { MatDialog } from '@angular/material/dialog';
import { ConfirmationModalComponent } from '@app/components/component/confirmation-modal/confirmation-modal.component';

@Component({
  selector: 'app-settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.css'],
})
export class SettingsComponent implements OnInit {
  footerMenus: any[] = [
    {
      id: 1,
      title: '',
      img: 'home.svg',
      slug: 'home',
      link: '/',
    },
    {
      id: 2,
      title: ' ',
      img: 'ic_last_read.svg',
      slug: 'ic_last_read',
      link: '/last-read-hadith',
    },
    {
      id: 3,
      title: '',
      img: 'ic_bookmark.svg',
      slug: 'ic_bookmark',
      link: '/mobile-hadith-bookmarks',
    },
    {
      id: 3,
      title: ' ',
      img: 'back.svg',
      slug: 'back',
      link: '/',
    },
  ];

  currentArText = 'false';
  loading = false;
  books: Book[] = [];
  selectedBook!: Book;

  constructor(
    private themeService: ThemeService,
    private indexeddbService: IndexeddbService,
    private helperService: HelperService,
    public dialog: MatDialog
  ) {}
  ngOnInit(): void {
    this.currentArText = this.themeService.getCurrentArabicText;
    if (!this.currentArText) {
      this.currentArText = 'true';
      this.themeService.setCurrentArabicText('true');
    }
    this.getBooks();
  }

  changeCurrentArabicText() {
    this.currentArText = this.currentArText === 'true' ? 'false' : 'true';
    this.themeService.setCurrentArabicText(this.currentArText);
  }

  getBooks(): void {
    this.loading = true;
    this.indexeddbService.getAll('books').subscribe((item) => {
      this.books = item
        .sort(this.helperService.sortBy('sequence'))
        .filter((value: Book) => {
          return value.downloaded;
        });
      console.log(this.books);
      this.loading = false;
    });
  }

  bookFullReset(book: Book): void {
    const instructionDialog = this.dialog.open(ConfirmationModalComponent, {
      data: `  "${book.title}"    ?     ,                      `,
      width: '350px',
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    });
    instructionDialog.afterClosed().subscribe((res) => {
      if (res) {
        this.books.forEach((bk, index) => {
          if (bk.slug === book.slug) {
            const updatedBook = JSON.parse(JSON.stringify(this.books[index]));
            updatedBook.downloaded = false;
            this.indexeddbService
              .update('books', updatedBook)
              .subscribe((i) => {
                console.log(i);
                this.getBooks();
              });
          }
        });
        this.indexeddbService
          .clear(book.slug + 'Chapters')
          .subscribe((value) => {
            console.log(value);
          });
        this.indexeddbService.clear(book.slug + 'Titles').subscribe((value) => {
          console.log(value);
        });
        this.indexeddbService
          .clear(book.slug + 'Hadiths')
          .subscribe((value) => {
            console.log(value);
            this.openDownloadDialog(book, 'all');
          });
      }
    });
  }

  bookChaptersReset(book: Book): void {
    const instructionDialog = this.dialog.open(ConfirmationModalComponent, {
      data: `  "${book.title}"     ?                        `,
      width: '350px',
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    });
    instructionDialog.afterClosed().subscribe((res) => {
      if (res) {
        this.books.forEach((bk, index) => {
          if (bk.slug === book.slug) {
            const updatedBook = JSON.parse(JSON.stringify(this.books[index]));
            updatedBook.downloaded = false;
            this.indexeddbService
              .update('books', updatedBook)
              .subscribe((i) => {
                console.log(i);
                this.getBooks();
              });
          }
        });
        this.indexeddbService
          .clear(book.slug + 'Chapters')
          .subscribe((value) => {
            this.openDownloadDialog(book, 'chapters');
          });
      }
    });
  }

  bookTitlesReset(book: Book): void {
    const instructionDialog = this.dialog.open(ConfirmationModalComponent, {
      data: `  "${book.title}"     ?                        `,
      width: '350px',
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    });
    instructionDialog.afterClosed().subscribe((res) => {
      if (res) {
        this.books.forEach((bk, index) => {
          if (bk.slug === book.slug) {
            const updatedBook = JSON.parse(JSON.stringify(this.books[index]));
            updatedBook.downloaded = false;
            this.indexeddbService
              .update('books', updatedBook)
              .subscribe((i) => {
                console.log(i);
                this.getBooks();
              });
          }
        });
        this.indexeddbService.clear(book.slug + 'Titles').subscribe((value) => {
          this.openDownloadDialog(book, 'titles');
        });
      }
    });
  }
  bookHadithsReset(book: Book): void {
    const instructionDialog = this.dialog.open(ConfirmationModalComponent, {
      data: `  "${book.title}"     ?                        `,
      width: '350px',
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    });
    instructionDialog.afterClosed().subscribe((res) => {
      if (res) {
        this.books.forEach((bk, index) => {
          if (bk.slug === book.slug) {
            const updatedBook = JSON.parse(JSON.stringify(this.books[index]));
            updatedBook.downloaded = false;
            this.indexeddbService
              .update('books', updatedBook)
              .subscribe((i) => {
                console.log(i);
                this.getBooks();
              });
          }
        });
        this.indexeddbService
          .clear(book.slug + 'Hadiths')
          .subscribe((value) => {
            this.openDownloadDialog(book, 'hadiths');
          });
      }
    });
  }

  openDownloadDialog(book: Book, type: string): void {
    this.dialog.open(DownloadHadithModalComponent, {
      data: {
        book,
        type,
      },
      width: '350px',
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    });

    this.indexeddbService.bookDownloadedObservable.subscribe((value) => {
      if (value.book === book.slug) {
        this.books.forEach((bk, index) => {
          if (bk.slug === book.slug) {
            const updatedBook = JSON.parse(JSON.stringify(this.books[index]));
            updatedBook.downloaded = true;
            this.indexeddbService
              .update('books', updatedBook)
              .subscribe((i) => {
                console.log(i);
                this.getBooks();
              });
          }
        });
      }
    });
  }
  deleteBook(book: Book): void {
    const instructionDialog = this.dialog.open(ConfirmationModalComponent, {
      data: `  "${book.title}"    ?`,
      width: '350px',
      autoFocus: false,
      scrollStrategy: new NoopScrollStrategy(),
    });
    instructionDialog.afterClosed().subscribe((res) => {
      if (res) {
        this.indexeddbService
          .clear(book.slug + 'Chapters')
          .subscribe((value) => {
            console.log(value);
          });
        this.indexeddbService.clear(book.slug + 'Titles').subscribe((value) => {
          console.log(value);
        });
        this.indexeddbService
          .clear(book.slug + 'Hadiths')
          .subscribe((value) => {
            this.books.forEach((bk, index) => {
              if (bk.slug === book.slug) {
                const updatedBook = JSON.parse(
                  JSON.stringify(this.books[index])
                );
                updatedBook.downloaded = false;
                this.indexeddbService
                  .update('books', updatedBook)
                  .subscribe((i) => {
                    console.log(i);
                    this.getBooks();
                  });
              }
            });
          });
      }
    });
  }

  updateDist() {
    // @ts-ignore
    AndroidNative.checkDistVersion();
  }
}
